<template>
  <div id="index">
    <Row>
      <Col span="12" class="header">
        <Card :bordered="false" :padding="0">
          <img src="../assets/header_bg.jpg">
          <div class="title">
            <h1>阿拉丁</h1>
            <time>2019.09.01</time>
          </div>
        </Card>
      </Col>
    </Row>
    <Row>
      <Col span="12" class="countdown">
        <h1>倒计时</h1>
        <p>大獎池24小時開壹次獎，每有壹筆新投入，時間將按照1个以太延長3小時计算，獎池100%的獎金將給予最後500名投資額最大玩家。小獎池24小時開壹次獎，100%開出，壹等獎1名直接奪得奖池的1/3，二等獎10名平分奖池的1/3，三等獎20名平分奖池的1/3。</p>
        <div class="big">
          <img src="../assets/eth.png" alt="">
          <p>大奖池</p>
          <time>24:00:00</time>
          <span>99999ETH</span>
        </div>
        <div class="small">
          <img src="../assets/eth.png" alt="">
          <p>小奖池</p>
          <time>24:00:00</time>
          <span>99999ETH</span>
        </div>
      </Col>
    </Row>
    <Row>
      <Col span="12" class="involvement">
        <Divider class="title">我要参与</Divider>
        <Card class="card" padding="0" :bordered="false">
          <Tabs class="tabs">
            <TabPane label="参与">
              <div class="inPlan">
                <p>当前燃烧比例 <span>1ETH = 166PPC</span></p>
                <ButtonGroup>
                  <Button>1ETH</Button>
                  <Button>11ETH</Button>
                  <Button>31ETH</Button>
                </ButtonGroup>
                <Input><span slot="append">ETH</span></Input>
                <Button type="success" long>SUBMIT</Button>
              </div>
            </TabPane>
            <TabPane label="门票">标签三的内容</TabPane>
            <TabPane label="推荐">标签二的内容</TabPane>
            <TabPane label="规则">标签三的内容</TabPane>
          </Tabs>
        </Card>
      </Col>
    </Row>
    <Row>
      <Col span="12" class="logs">
        <Divider class="title">统计数据</Divider>
        <Card class="card infoList" padding="0" :bordered="false">
          <CellGroup>
            <Cell title="已結算分紅收益" extra="-- ETH" />
            <Cell title="推荐奖励" extra="-- ETH" />
            <Cell title="奖池收益" extra="-- ETH" />
            <Cell title="下次分红收益时间差" extra="--:--:--" />
            <Cell title="动静出局还差" extra="-- ETH" />
            <Cell title="3倍未结算预计收益" extra="-- ETH 结算" />
            <Cell title="我的业绩收益" extra="-- ETH 提现" />
            <Cell title="未提币数量" extra="-- ETH" />
            <Cell>
              <Button type="success" long> 提 现 收 益 </Button>
            </Cell>
          </CellGroup>
        </Card>
        <Card class="card infoList" padding="0" :bordered="false">
          <CellGroup>
            <Cell title="大奖池当前总量" extra="-- ETH" />
            <Cell title="小奖池当前总量" extra="-- ETH" />
            <Cell title="门票焚毁倍率" extra="-- TIMES" />
            <Cell title="已焚毁门票" extra="--" />
            <Cell title="已投入的总资金" extra="-- ETH" />
            <Cell title="总已结算分红收益" extra="-- ETH" />
            <Cell title="总的推荐收益" extra="-- ETH" />
            <Cell title="团队" extra="-- ETH 提现" />
            <Cell title="大使" extra="-- ETH 提现" />
          </CellGroup>
        </Card>
        <Card class="card jcList" padding="0" :bordered="false">
          <p>大奖池</p>
          <CellGroup>
            <Cell title="No.1" extra="-- ETH" />
          </CellGroup>
          <p>小奖池</p>
          <CellGroup>
            <Cell title="No.1" extra="-- ETH" />
          </CellGroup>
        </Card>
      </Col>
    </Row>
    <button @click="send('number')">alsdkfja</button>
    index
    <div id="animated-number-demo">
      <input v-model.number="number" type="number" step="20">
      <p>{{ animatedNumber }}</p>
    </div>
    <p v-for="i in msg">
      {{i}}
    </p>

  </div>

</template>

<script>
    export default {
        name: "index",
        data() {
            return {
                number: 0,
                tweenedNumber: 0,
                path: "ws://192.168.0.240:3001",
                socket: "",
                msg: []
            }
        },
        computed: {
            animatedNumber: function () {
                return this.tweenedNumber.toFixed(0);
            }
        },
        watch: {
            number: function (newValue) {
                window.TweenLite.to(this.$data, 1, {tweenedNumber: newValue});
            }
        },
        mounted() {
            // 初始化
            this.init()
        },
        methods: {
            init: function () {
                if (typeof (WebSocket) === "undefined") {
                    alert("您的浏览器不支持socket")
                } else {
                    // 实例化socket
                    this.socket = new WebSocket(this.path)
                    // 监听socket连接
                    this.socket.onopen = this.open
                    // 监听socket错误信息
                    this.socket.onerror = this.error
                    // 监听socket消息
                    this.socket.onmessage = this.getMessage
                }
            },
            open: function () {
                console.log("socket连接成功")
            },
            error: function () {
                console.log("连接错误")
            },
            getMessage: function (msg) {
                this.msg.push(msg.data);
            },
            send: function (msg) {
                this.socket.send(msg)

            },
            close: function () {
                console.log("socket已经关闭")
            }
        },
        destroyed() {
            // 销毁监听
            this.socket.onclose = this.close
        },
    }
</script>

<style lang="less">
  #index {
    .title{
      color: #008c7c;
      padding: 0 3rem;
      span{
        font-size: 1.4rem;
      }
    }
    .header {
      position: relative;
      width: 100%;
      img{
        width: 100%;
        float: left;
      }
      .title{
        text-align: center;
        position: absolute;
        margin-top: 14%;
        top: 0;
        left: 0;
        right: 0;
        color: #ffffff;
        h1,time{
          text-shadow: 3px 3px 4px #000000;
        }
        h1{
          font-size: 3rem;
          font-weight: 400;
        }
        time{
          margin-top: 1rem;
          border-top: 1px solid #ffffff;
          border-bottom: 1px solid #ffffff;
          width: 16rem;
          display: inline-block;
          padding: .5rem 0;
          font-size: 1.2rem;
        }
      }
      img {
        margin: 0;
      }
    }
    .countdown{
      width: 100%;
      background: rgba(19,7,104,.3);
      overflow: hidden;
      padding: 1rem;
      text-align: center;
      &>h1{
        font-size: 2.4rem;
        text-align: center;
        margin: 2rem;
      }
      &>p{
        text-indent: 2rem;
        line-height: 1.5rem;
        font-size: .9rem;
        text-align: left;
      }
      img{
        position: absolute;
        left: 8%;
        z-index: 1;
        opacity: 1;
        top: 40%;
        height: 4rem;
      }
      .big{
        color: #c5085b;
        border: 1px solid #C5085B;
      }
      .small{
        color: #bcbc37;
        border: 1px solid #BCBC37;
        margin-bottom: 2rem;
      }
      &>div{
        position: relative;
        width: 80%;
        display: inline-block;
        padding: 10px;
        margin-top: 3rem;
        border-radius: 7px;
        time,span{
          position: relative;
          display: block;
          z-index: 2;
        }
        p{
          font-size: 1.2rem;
        }
        time{
          font-size: 2.4rem;
        }
        span{
          font-size: 2rem;
          line-height: 2rem;
        }
      }
    }
    .involvement{
      width: 100%;
      .card{
        width: 90%;
        margin: auto;
        padding: 0;
        box-shadow: 0 0 10px 3px #00D7BD;
        overflow: hidden;
        background: #2a2a2a;
      }
      .tabs{
        .ivu-tabs-nav-scroll{
          background: #000000;
          .ivu-tabs-nav{
            width: 100%;
            .ivu-tabs-ink-bar{
              background-color: #CA0000;
            }
            .ivu-tabs-tab{
              text-align: center;
              color: #ffffff;
            }
            .ivu-tabs-tab-active{
              color: #CA0000;
            }
          }
        }
        .ivu-tabs-content{
          .inPlan{
            padding: 0 20px 20px;
          }
        }
      }
    }
    .logs {
      width: 100%;
      .card{
        border-radius: 0;
        background: rgba(0,102,160,.3);
        &+.card{
          margin-top: 10px;
        }
        div{
          color: #008c7c;
        }
      }
    }
  }
</style>



